<template>
<div align="center">
    <ul class="nav nav-pills" >
        <router-link to="/about" tag="li" activeclass="active" exact><a>About</a></router-link>
        <router-link to="/bookmark" tag="li" activeclass="active"><a>Bookmark</a></router-link>
        <router-link to="/hello" tag="li" activeclass="active"><a>Profile</a></router-link>  
        <li role="presentation"><a @click="logout()">Sign out</a></li>      
    </ul>
</div>
</template>

<script>
import firebase from 'firebase';
export default {
    name: 'Header',
    methods: {
        logout() {
            firebase.auth().signOut().then( () => {
                this.$router.push( { name: 'Login'});
            })
        }
    },
}
</script>

<style scoped>
ul {
    width: 70%;
    margin: auto;
}
</style>




